.WorksPanel {
  height: 100vh;
  width: 100vw;
  text-align: center;
  position: relative;
  display:table;
}
.WorksPanelBlurb {
  width: 120%;
  height: 100%;
  display: table-cell;
  vertical-align: middle;
  text-align: center;  
  background: #0025f8;
  -webkit-box-shadow: inset 0em 5em 5em 0 rgb(3, 18, 150);
          box-shadow: inset 0em 5em 5em 0 rgb(3, 18, 150);
}
#WorkContainer {
  width: calc(80% - 10em); /* 10 em to represent the widest navigation item's number of characters */
  height: 80%;
  text-align: left;
  border-style: solid;
  border-color: gray;
  border-width: 1px;
  background-color: #66b98a;
  margin: auto;
  padding: 1%;
  overflow: scroll;
}


