.KnowledgePanel {
  height: 100vh;
  width: 100vw;
  text-align: center;
  position: relative;
  display:table;
}
.KnowledgePanelBlurb {
  width: 120%;
  height: 100%;
  display: table-cell;
  vertical-align: middle;
  text-align: center;
  background: #0a22ff;
}

#KnowledgeContainer {
  width: calc(80% - 10em); /* 10 em to represent the widest navigation item's number of characters */
  height: 80%;
  text-align: left;
  border-style: solid;
  border-color: gray;
  border-width: 1px;
  background-color: #66b98a;
  margin: auto;
  padding: 1%;
  overflow: scroll;
}


